.msa {
    position: relative;
    overflow-x: scroll;
    line-height:24px;
    font-size: 16px;
    font-family: 'msaplain', monospace;
    white-space: nowrap;
    /*touch-action: pinch-zoom pan-x;*/
}

.rc-slider-track {
    background-color: #7a8181;
}
.rc-slider-rail {
    background-color: #7a8181;
}

