.splice-junction {
    width: 0.2ch;
    height: 24px;
    transform: translateX(-0.1ch) scaleX(var(--residues-per-pixel));
    background: red;
    position: absolute;
}
.gap-vline {
    width: 1px;
    transform: translate(-0.5px);
    background: blue;
    position: absolute;
}
.closed-gap {
    position: absolute;
    width: 0;
    height: 0;
    top: 11px;
    border-top: 1ch solid blue;
    border-left: 1ch solid transparent;
    border-right: 1ch solid transparent;
}
.closed-gap:hover {
  cursor: pointer;
}
.open-gap-right {
    position: absolute;
    width: 0;
    height: 0;
    top: 11px;
    border-top: 1ch solid blue;
    border-right: 1ch solid transparent;
}
.open-gap-center {
    position: absolute;
    top: 11px;
    background: lightblue;
    border-top: 1px solid blue;
    border-bottom: 1px solid blue;
    height:1ch;
}
.open-gap-center:hover {
  cursor: pointer;
}
.open-gap-left {
    position: absolute;
    width: 0;
    height: 0;
    top: 11px;
    border-top: 1ch solid blue;
    border-left: 1ch solid transparent;
}
